<template>
  <div class="greet">
    <div class="greet-container">
      <div class="greet-content">freedom to</div>
      <div class="greet-content">learn</div>
      <div class="greet-content">English</div>
    </div>
  </div>
</template>

<script>
  // import { mapGetters } from 'vuex'
  export default {
    name: 'greet',
    data () {
      return {}
    },
    created: function () {
    },
    methods: {}
  }
</script>

<style lang="scss" scoped>
  @import './../scss/variables';
  .greet{
    position: fixed;
    width: 100%;
    height: 100%;
    z-index: 10;
    top: 0;
    left: 0;
    font-family: $greetFont;
    line-height: 1;
    color: #000;
    display: flex;
    align-items: center;
    justify-content: center;
    @include media(minTablet){
      font-size: 45px;
    }
    @include media(maxTablet){
      font-size: 11vw;
    }
    &-container{
      padding: 0 0 30px;
    }
    &-content{
      &:nth-child(2){
        padding-left: 20px;
      }
      &:nth-child(3){
        padding-left: 40px;
      }
      &:first-letter{
        text-transform: uppercase;
        color: #fff;
      }
    }
  }
</style>



