<template>
  <div class="">
    <div class="title-container">
      <h3>{{ $t('New folder') }}</h3>
    </div>
    <p>{{ $t('Enter the title') }}</p>
    <div class="input">
      <input type="text" v-model.trim="folderName" @keyup.13="addAndGo()" ref='input'>
      <transition
        mode="out-in"
        name="fade"
        enter-active-class="fadeIn"
        leave-active-class="fadeOut">
        <div v-if="!folderName && showMessages" class="error">{{ $t('The field is not filled') }}</div>
      </transition>
    </div>
    <div>
      <a @click.prevent="addNew" class="button">{{ $t('add') }}</a>
      <a @click.prevent="addAndGo" class="button">{{ $t('add and go') }}</a>
    </div>
    <transition
      mode="out-in"
      name="fade"
      enter-active-class="fadeIn"
      leave-active-class="fadeOut">
      <div v-if="popupMessage" class="popup">
        <div class="popup-wrap">
          <div class="popup-container">
            <a @click.prevent="popupMessage = false" class="popup-close"></a>
            <div class="popup-content">
              <h4>{{ $t('New folder added') }}</h4>
            </div>
          </div>
        </div>
      </div>
    </transition>
  </div>
</template>

<script>
  import { mapGetters } from 'vuex'
  export default {
    data () {
      return {
        folderName: '',
        popupMessage: false,
        showMessages: false
      }
    },
    mounted: function () {
      this.$refs.input.focus()
    },
    computed: {
      ...mapGetters({
        wordBook: 'getWordBook'
      }),
      folderId () {
        let len = this.wordBook.folders.length - 1
        return this.wordBook.folders[len].id + 1
      }
    },
    methods: {
      addFolder () {
        this.wordBook.folders.push({
          name: this.folderName,
          removable: true,
          id: this.folderId
        })
        this.$store.dispatch('setWordBook', this.wordBook)
        this.folderName = ''
        this.showMessages = false
      },
      addNew () {
        if (this.folderName) {
          this.addFolder()
          this.popupMessage = true
          let $this = this
          this._delayRunTimer = setTimeout(function () {
            $this.popupMessage = false
          }, 1500)
        } else {
          this.showMessages = true
        }
      },
      addAndGo () {
        if (this.folderName) {
          let id = this.folderId
          this.addFolder()
          this.$emit('setId', id)
        } else {
          this.showMessages = true
        }
      }
    }
  }
</script>

<style lang="scss" scoped>
  @import './../scss/variables';
  p{margin: 5px 0;}
  .input{
    margin: 0 0 25px;
    position: relative;
  }
  .fade-leave-to{
    animation-duration: 0.5s;
  }
  .fade-enter-to{
    animation-duration: 0.5s;
  }
  .popup h4 {
    margin: 0;
  }
</style>



