<template>
  <div class="wordBook">
    <div class="wordBook-nav">
      <a @click.prevent="component = 'folders'" :class="{ active: component === 'folders' }" class="button">
        <icon name="folder-open-o" scale="1"></icon>
        {{ $t('folders') }}
      </a>
      <a @click.prevent="component = 'newFolder'" :class="{ active: component === 'newFolder' }" class="button">
        <icon name="folder" scale="1"></icon>
        {{ $t('new folder') }}
      </a>
    </div>
    <transition
      mode="out-in"
      name="fade"
      enter-active-class="fadeIn"
      leave-active-class="fadeOut">
      <folders v-if="component === 'folders'" @setId="openFolder"></folders>
      <folder v-else-if="component === 'folder'" :id="folderId" @closeFolder="component = 'folders'"></folder>
      <new-folder v-if="component === 'newFolder'" @setId="openFolder"></new-folder>
    </transition>
  </div>
</template>

<script>
  import { mapGetters } from 'vuex'
  import 'vue-awesome/icons/folder-open-o'
  import 'vue-awesome/icons/folder'
  import 'vue-awesome/icons/folder-o'
  import 'vue-awesome/icons/reply'
  import folders from './folders'
  import Folder from './folder'
  import newFolder from './newFolder'
  export default {
    data () {
      return {
        component: 'folders',
        folderId: '',
        showFolder: ''
      }
    },
    components: {
      folders,
      Folder,
      newFolder
    },
    computed: {
      ...mapGetters({
        wordBook: 'getWordBook'
      })
    },
    created: function () {
      // console.log('This., -/ is #! an $ % ^ & * example ;: {} of a = -_ string with `~)() punctuation'.replace(/[^A-Za-z--]/g, ''))
    },
    methods: {
      openFolder (id) {
        this.folderId = id
        this.component = 'folder'
      }
    }
  }
</script>

<style lang="scss" scoped>
  @import './../scss/variables';
  .fade-leave-to{
    animation-duration: 0.5s;
  }
  .fade-enter-to{
    animation-duration: 0.5s;
  }
  .wordBook{
    &-nav{
      margin: 10px 0;
      display: flex;
      justify-content: space-between;
      a{
        width: calc(50% - 5px);
      }
    }
  }
  .fa-icon{
    vertical-align: middle;
    margin: 0 5px 0 0;
  }
</style>



