<template>
  <div class="training">
    <template v-if="wordBook.words.length">
      <div class="training-nav">
        <label>
          {{ $t('Folder') }}:
          <select v-model="trainFolder" @change="setTrainFolder()">
            <option
              v-for="(folder, index) in wordBook.folders"
              :key="index"
              :value="folder.id"
              v-if="foldersLength[index]">{{ $t(folder.name) }}</option>
          </select>
        </label>
        <a @click.prevent="popupCounter = true" class="counter">{{ $t('counter') }} {{ counter }}</a>
      </div>
      <div class="training-task">
        <div class="training-task-word">
          <template v-if="help">{{ taskWord.en }}</template>
          <template v-else>{{ taskWord.ru }}</template>
        </div>
        <div class="dropdown">
          <a v-click-outside="hide" @click.prevent="wordRedactor = true" class="navButton"><span></span></a>
          <div v-if="wordRedactor" class="dropdown-container">
            <a @click.prevent="editWord()">{{ $t('edit') }}</a>
            <a @click.prevent="popupDeleteWord = true">{{ $t('delete') }}</a>
          </div>
        </div>
      </div>
      <div class="training-input">
        <input type="text" v-model.trim="answer" ref="input">
        <a @click.prevent="showHint()" class="button">?</a>
      </div>

      <transition
        mode="out-in"
        name="fade"
        enter-active-class="fadeIn"
        leave-active-class="fadeOut">
        <div v-if="popupCounter" class="popup">
          <div class="popup-wrap">
            <div class="popup-container">
              <a @click.prevent="popupCounter = false" class="popup-close"></a>
              <div class="popup-content">
                <h4>{{ $t('Reset the counter?') }}</h4>
                <a @click.prevent="clearCounter()" class="button">{{ $t('reset') }}</a>
                <a @click.prevent="popupCounter = false" class="button">{{ $t('cancel') }}</a>
              </div>
            </div>
          </div>
        </div>
        <div v-if="popupDeleteWord" class="popup">
          <div class="popup-wrap">
            <div class="popup-container">
              <a @click.prevent="popupDeleteWord = false" class="popup-close"></a>
              <div class="popup-content">
                <h4>{{ $t('Confirm the removal of the word') }} {{taskWord.en }} </h4>
                <a @click.prevent="deleteWord()" class="button">{{ $t('delete') }}</a>
                <a @click.prevent="popupDeleteWord = false" class="button">{{ $t('cancel') }}</a>
              </div>
            </div>
          </div>
        </div>
      </transition>
    </template>
    <template v-else-if="!wordBook.words.length">
      <div class="training-empty">
        <div class="title-container">
          <h3>{{ $t('You have no words to train') }}</h3>
        </div>
        <a @click.prevent="addWord" class="button">{{ $t('add word') }}</a>
      </div>
    </template>
  </div>
</template>

<script>
  import { mapGetters } from 'vuex'
  import ClickOutside from 'vue-click-outside'
  export default {
    data () {
      return {
        answer: '',
        counter: 0,
        popupCounter: false,
        popupDeleteWord: false,
        trainFolder: 1,
        taskWord: '',
        help: false,
        wordRedactor: false
      }
    },
    directives: {
      ClickOutside
    },
    computed: {
      ...mapGetters({
        wordBook: 'getWordBook'
      }),
      foldersLength () {
        let arr = []
        for (let i of this.wordBook.folders) {
          let thisId = i.id
          let wordArr = this.wordBook.words.filter(item => {
            if (item.folder === thisId) {
              return true
            }
          })
          arr.push(wordArr.length)
        }
        return arr
      },
      collection () {
        let arr = this.wordBook.words.filter(item => {
          if (item.folder === this.trainFolder) {
            return true
          }
        })
        return arr
      }
    },
    created: function () {
      let localCounter = JSON.parse(window.localStorage.getItem('counter'))
      if (localCounter) {
        this.counter = localCounter
      }

      let localTrainFolder = JSON.parse(window.localStorage.getItem('trainFolder'))
      this.trainFolder = localTrainFolder || 1
      this.checkTrainFolder()

      if (this.wordBook.words.length) {
        this.setTask()
      }
    },
    methods: {
      clearCounter () {
        this.counter = 0
        this.popupCounter = false
      },
      checkTrainFolder () {
        // this.wordBook.folders.find((x, i) => x.id === thisId)
        let folderIndex = this.wordBook.folders.findIndex((x, i) => x.id === this.trainFolder)
        if (!this.foldersLength[folderIndex]) {
          for (let i in this.wordBook.folders) {
            if (this.foldersLength[i]) {
              this.trainFolder = this.wordBook.folders[i].id
              window.localStorage.setItem('trainFolder', JSON.stringify(this.trainFolder))
              return false
            }
          }
        }
      },
      setTrainFolder () {
        window.localStorage.setItem('trainFolder', JSON.stringify(this.trainFolder))
        this.setTask()
      },
      setTask () {
        let i = Math.floor(Math.random() * this.collection.length)
        this.taskWord = JSON.parse(JSON.stringify(this.collection[i]))
        this.help = false
      },
      hide () {
        this.wordRedactor = false
      },
      addWord () {
        this.$store.dispatch('setCurrentComponent', 'addWord')
      },
      editWord () {
        this.$store.dispatch('setNewWord', this.taskWord)
        this.addWord()
      },
      deleteWord () {
        this.wordBook.words = this.wordBook.words.filter(item => {
          if (item.en !== this.taskWord.en) {
            return true
          }
        })
        this.$store.dispatch('setWordBook', this.wordBook)
        this.checkTrainFolder()
        this.setTask()
        this.popupDeleteWord = false
      },
      showHint () {
        this.help = !this.help
        this.$refs.input.focus()
      }
    },
    watch: {
      'counter': function (val) {
        window.localStorage.setItem('counter', JSON.stringify(val))
      },
      'answer': function (val) {
        if (val) {
          let $this = this
          clearTimeout(this._delayRunTimer)
          this._delayRunTimer = setTimeout(() => {
            let cleared = val.toLowerCase().replace(/[\s{2}]+/g, ' ')
            if (cleared === $this.taskWord.en) {
              $this.setTask()
              $this.answer = ''
              $this.counter++
            }
          }, 700)
        }
      }
    }
  }
</script>

<style lang="scss" scoped>
  @import './../scss/variables';
  .fade-leave-to{
    animation-duration: 0.5s;
  }
  .fade-enter-to{
    animation-duration: 0.5s;
  }
  .training{
    &-nav{
      display: flex;
      justify-content: space-between;
      margin: 20px 0 0 0;
      font-size: 11px;
      label{
        flex: 1 1 auto;
      }
      select{
        display: inline-block;
        vertical-align: middle;
        margin: 0 0 0 5px;
        padding: 0;
        background: transparent;
        width: auto;
        border: 0;
      }
      .counter{
        min-width: 55px;
      }
    }
    &-input{
      display: flex;
      input{
        flex: 1 1 auto;
        @include media(maxTablet){font-size: 5vw;}
      }
      .button{
        @include media(maxTablet) {
          font-size: 4vw;
          padding: 0 3%;
          display: flex;
          align-items: center;
        }
      }
    }
    &-task{
      position: relative;
      padding: 0 24px;
      margin: 20px 0;
      /*height: 38px;*/
      display: flex;
      align-items: center;
      line-height: 1.2;
      &-word{
        /*max-height: 100%;*/
        /*overflow: hidden;*/
        text-align: center;
        width: 100%;
        @include media(maxTablet){font-size: 5vw;}
      }
      .dropdown{
        position: absolute;
        right: 0;
        top: 50%;
        transform: translateY(-50%);
      }
    }
    &-empty{
      text-align: center;
    }
  }
</style>



