<template>
  <div class="folder">
    <div>
      <div class="title-container">
        <icon name="folder-o" scale="2"></icon>
        <h3>{{ $t(title) }}</h3>
        <div v-if="removable" class="dropdown">
          <a v-click-outside="hide" @click.prevent="opened = true" class="navButton"><span></span></a>
          <div v-if="opened" class="dropdown-container">
            <a @click.prevent="rename = true">{{ $t('Rename the folder') }}</a>
            <a @click.prevent="deleteNote = true, rename = false, showMessages = false">{{ $t('Delete the folder') }}</a>
          </div>
        </div>
      </div>
      <div class="folder-rename" v-if="rename">
        <span>{{ $t('New name of folder') }}</span>
        <div class="folder-input">
          <input type="text" v-model="newName">
          <transition
            mode="out-in"
            name="fade"
            enter-active-class="fadeIn"
            leave-active-class="fadeOut">
            <div v-if="!newName && showMessages" class="error">{{ $t('The field is not filled') }}</div>
          </transition>
        </div>
        <a @click.prevent="saveNewName" class="button">{{ $t('save') }}</a>
        <a @click.prevent="cancelEdit" class="button">{{ $t('cancel') }}</a>
      </div>
    </div>
    <ul v-if="collection.length" class="list">
      <li v-for="(item, index) in collection" :key="index">
        <div class="list-content">
          <strong>{{ item.en }}</strong> -
          <span>{{ item.ru }}</span>
          <a @click.prevent="openNav(index)"
             :class="{ opened: openIndex === index }"
             class="navButton"><span></span></a>
        </div>
        <div v-if="openIndex === index" class="list-buttons">
          <a @click.prevent="deleteWord(item.en)" class="button">{{ $t('delete') }}</a>
          <a @click.prevent="editWord(item)" class="button">{{ $t('edit') }}</a>
        </div>
      </li>
    </ul>
    <div v-else class="emptyFolder">{{ $t('This folder is empty') }}</div>

    <transition
      mode="out-in"
      name="fade"
      enter-active-class="fadeIn"
      leave-active-class="fadeOut">
      <div v-if="deleteNote" class="popup">
        <div class="popup-wrap">
          <div class="popup-container">
            <a @click.prevent="deleteNote = false" class="popup-close"></a>
            <div class="popup-content">
              <h4>
                <template v-if="collection.length">{{ $t('The folder will be deleted with all words what contains in this folder') }}</template>
                <template v-else>{{ $t('Confirm removal') }}</template>
              </h4>
              <a @click.prevent="deleteFolder" class="button">{{ $t('delete') }}</a>
              <a @click.prevent="deleteNote = false" class="button">{{ $t('cancel') }}</a>
            </div>
          </div>
        </div>
      </div>
    </transition>
  </div>
</template>

<script>
  import { mapGetters } from 'vuex'
  import ClickOutside from 'vue-click-outside'
  import 'vue-awesome/icons/folder-o'
  export default {
    props: ['id'],
    data () {
      return {
        rename: false,
        opened: false,
        deleteNote: false,
        newName: '',
        showMessages: false,
        openIndex: ''
      }
    },
    directives: {
      ClickOutside
    },
    computed: {
      ...mapGetters({
        wordBook: 'getWordBook',
        newWord: 'getNewWord'
      }),
      removable () {
        let thisId = this.id
        if (thisId) {
          let status = this.wordBook.folders.find(x => x.id === thisId).removable
          return status
        } else {
          return false
        }
      },
      title () {
        if (this.id === 0) {
          return 'All'
        } else {
          let thisId = this.id
          let name = this.wordBook.folders.find(x => x.id === thisId).name
          return name
        }
      },
      collection () {
        let array = []
        if (this.id === 0) {
          array = this.wordBook.words
        } else {
          let $this = this
          array = this.wordBook.words.filter(function (item) {
            if (item.folder === $this.id) {
              return item
            }
          })
        }
        return array.sort(this.sortBy)
      }
    },
    methods: {
      sortBy (a, b) {
        a.en.replace(/[^A-Za-z--]/g, '')
        b.en.replace(/[^A-Za-z--]/g, '')
        if (a.en > b.en) {
          return 1
        }
        if (a.en < b.en) {
          return -1
        }
        return 0
      },
      cancelEdit () {
        this.rename = false
        this.newName = ''
        this.showMessages = false
      },
      saveNewName () {
        if (this.newName) {
          let thisId = this.id
          this.wordBook.folders = this.wordBook.folders.map(item => {
            if (item.id === thisId) {
              item.name = this.newName
            }
            return item
          })
          this.$store.dispatch('setWordBook', this.wordBook)
          this.cancelEdit()
        } else {
          this.showMessages = true
        }
      },
      hide () {
        this.opened = false
      },
      deleteFolder () {
        let $this = this
        if (this.collection.length) {
          this.wordBook.words = this.wordBook.words.filter(function (item) {
            if (item.folder !== $this.id) {
              return item
            }
          })
        }
        this.wordBook.folders = this.wordBook.folders.filter(function (item) {
          if (item.id !== $this.id) {
            return item
          }
        })
        this.$store.dispatch('setWordBook', this.wordBook)
        this.$emit('closeFolder')
      },
      openNav (index) {
        this.openIndex = this.openIndex === index ? '' : index
      },
      editWord (word) {
        let editable = JSON.parse(JSON.stringify(word))
        this.$store.dispatch('setNewWord', editable)
        this.$store.dispatch('setCurrentComponent', 'addWord')
      },
      deleteWord (word) {
        this.wordBook.words = this.wordBook.words.filter(item => {
          if (item.en !== word) {
            return true
          }
        })
        this.$store.dispatch('setWordBook', this.wordBook)
      }
    }
  }
</script>

<style lang="scss" scoped>
  @import './../scss/variables';
  .title-container{
    display: flex;
    width: 100%;
    box-sizing: border-box;
    padding: 0 25px 0 0;
    position: relative;
    margin: 20px 0;
    h3{
      margin: 0;
      flex: 1 1 auto;
      text-align: left;
      padding: 6px 0 0 5px;
    }
    .navButton span{background: #9c9c9c;}
    .fa-icon {
      color: $borderColor;
    }
  }
  .dropdown{
    position: absolute;
    top: 5px;
    right: 0;
    &-container{width: 200px;}
  }
  .emptyFolder{
    text-align: center;
    font-size: 1.2rem;
    margin: 30px 0 0;
  }
  .fade-leave-to{
    animation-duration: 0.5s;
  }
  .fade-enter-to{
    animation-duration: 0.5s;
  }
  .folder{
     &-rename{
       margin: 20px 0;
     }
    &-input{
      margin: 5px 0 20px;
      position: relative;
    }
  }
  .list {
    margin: 40px 0;
    padding: 0;
    list-style: none;
    counter-reset: list;
    li{
      border-top: 1px solid $borderColor;
      position: relative;
      padding: 7px 0;
      display: block;
      margin: 0;
      &:before {
        counter-increment: list;
        content: counter(list);
        color: #9c9c9c;
        position: absolute;
        top: 8px;
        left: 0;
        width: 22px;
        text-align: center;
        font-size: 14px;
      }
    }
    .navButton{
      position: absolute;
      top: 3px;
      right: 0;
      span{background: #9c9c9c;}
    }
    &-content{
      line-height: 1;
      padding: 0 25px;
    }
    &-buttons{
      padding: 15px 0 3px;
      display: flex;
      a {
        flex: 1 1 50%;
        & + a{
          border-left: 1px solid #fff;
        }
      }
    }
  }
</style>



