<template>
  <div class="addWord">
    <div class="title-container">
      <h3 v-if="oldWord === '' && !exist">{{ $t('New word') }}</h3>
      <h3 v-else>{{ $t('Editing') }}</h3>
    </div>
    <div>{{ $t('Word') }}</div>
    <div class="addWord-input">
      <input v-model.trim="newWord.en" type="text">
      <transition
        mode="out-in"
        name="fade"
        enter-active-class="fadeIn"
        leave-active-class="fadeOut">
        <div v-if="oldWord === '' && exist" class="error">{{ $t('This word already exists') }}</div>
        <div v-else-if="!newWord.en && showMessages" class="error">{{ $t('The field is not filled') }}</div>
      </transition>
    </div>
    <div>{{ $t('Transfer') }}</div>
    <div class="addWord-input">
      <input v-model.trim="newWord.ru" type="text">
      <transition
        mode="out-in"
        name="fade"
        enter-active-class="fadeIn"
        leave-active-class="fadeOut">
        <div v-if="!newWord.ru && showMessages" class="error">{{ $t('The field is not filled') }}</div>
      </transition>
    </div>
    <div>{{ $t('Folder') }}</div>
    <div class="addWord-input">
      <select v-model="newWord.folder">
        <option v-for="(folder, index) in wordBook.folders" :key="index" :value="folder.id">{{ $t(folder.name) }}</option>
      </select>
      <transition
        mode="out-in"
        name="fade"
        enter-active-class="fadeIn"
        leave-active-class="fadeOut">
        <div v-if="!newWord.folder && showMessages" class="error">{{ $t('Select folder') }}</div>
      </transition>
    </div>
    <a @click.prevent="checkForm('keep')" class="button" v-if="oldWord === '' && !exist">{{ $t('keep') }}</a>
    <a @click.prevent="checkForm('replace')" class="button" v-else>{{ $t('replace') }}</a>
    <a @click.prevent="clear()" class="button">{{ $t('clear') }}</a>

    <transition
      mode="out-in"
      name="fade"
      enter-active-class="fadeIn"
      leave-active-class="fadeOut">
      <div v-if="popupMessage" class="popup">
        <div class="popup-wrap">
          <div class="popup-container">
            <a @click.prevent="deleteNote = false" class="popup-close"></a>
            <div class="popup-content">
              <h3>{{ $t('Saved') }}</h3>
            </div>
          </div>
        </div>
      </div>
    </transition>
  </div>
</template>

<script>
  import { mapGetters } from 'vuex'
  export default {
    data () {
      return {
        oldWord: '',
        // exist: false,
        showMessages: false,
        popupMessage: false
      }
    },
    computed: {
      ...mapGetters({
        wordBook: 'getWordBook',
        newWord: 'getNewWord'
      }),
      exist () {
        let word = this.newWord.en.toLowerCase()
        return this.wordBook.words.some(x => x.en === word)
      }
    },
    created: function () {
      this.oldWord = this.newWord.en
    },
    methods: {
      clear () {
        this.newWord.en = ''
        this.newWord.ru = ''
        this.newWord.folder = ''
        if (this.oldWord !== '') {
          this.$store.dispatch('setNewWord', this.newWord)
        }
        this.oldWord = ''
      },
      checkForm (func) {
        if (this.newWord.en !== '' && this.newWord.ru !== '' && this.newWord.folder !== '') {
          if (func === 'keep') {
            this.keep()
          } else if (func === 'replace') {
            this.replace()
          }
          this.setWords()
          this.popupMessage = true
          let $this = this
          this._delayRunTimer = setTimeout(function () {
            $this.popupMessage = false
          }, 1500)

          this.showMessages = false
        } else {
          this.showMessages = true
        }
      },
      keep () {
        this.wordBook.words.push(
          {
            en: this.newWord.en.toLowerCase(),
            ru: this.newWord.ru.toLowerCase(),
            folder: this.newWord.folder
          }
        )
      },
      replace () {
        // this.wordBook.words = this.wordBook.words.map(item => item.en === this.oldWord ? this.newWord : item)
        let test = this.oldWord ? this.oldWord : this.newWord.en.toLowerCase()
        this.wordBook.words = this.wordBook.words.map(item => {
          if (item.en === test) {
            return {
              en: this.newWord.en.toLowerCase(),
              ru: this.newWord.ru.toLowerCase(),
              folder: this.newWord.folder
            }
          } else {
            return item
          }
        })
      },
      setWords () {
        this.$store.dispatch('setWordBook', this.wordBook)
        this.clear()
      }
    }
  }
</script>

<style lang="scss" scoped>
  @import './../scss/variables';
  .fade-leave-to, .fade-enter-to{
    animation-duration: 0.3s;
  }
  .addWord{
    &-input{
      position: relative;
      margin-bottom: 20px;
      margin-top: 5px;
    }
  }
  .button{margin: 20px 0 0 0;}
</style>



