<template>
  <div id="app" :class="currentTheme">
    <main class="main">
      <!--<img src="./assets/logo.png" alt="Vue.js PWA">-->
      <transition
        mode="out-in"
        name="fade"
        enter-active-class="fadeIn"
        leave-active-class="fadeOut">
        <greet v-if="showGreet"></greet>
        <div v-else>
          <header class="header">
            <a v-for="(item, index) in nav"
               :key="index"
               :class="{ active: item.name === currentComponent }"
               @click.prevent="changeComponent(item.name)">
              <icon :name="item.icon"></icon>
            </a>
          </header>
          <transition
            mode="out-in"
            name="fade"
            enter-active-class="fadeIn"
            leave-active-class="fadeOut">
            <training v-if="currentComponent === 'training'"></training>
            <languages v-else-if="currentComponent === 'language'"></languages>
            <word-book v-else-if="currentComponent === 'wordBook'"></word-book>
            <add-word v-else-if="currentComponent === 'addWord'"></add-word>
          </transition>
        </div>
      </transition>
    </main>
  </div>
</template>

<script>
  import { mapGetters } from 'vuex'
  import Greet from './components/Greet'
  import training from './components/training'
  import wordBook from './components/wordBook'
  import addWord from './components/addWord'
  import Languages from './components/Languages'

  import 'vue-awesome/icons/home'
  import 'vue-awesome/icons/book'
  import 'vue-awesome/icons/file-text-o'
  import 'vue-awesome/icons/adjust'
  import 'vue-awesome/icons/wrench'
  export default {
    name: 'app',
    components: {
      Greet,
      training,
      wordBook,
      addWord,
      Languages
    },
    data () {
      return {
        showGreet: true,
        nav: [
          {
            name: 'training',
            icon: 'home'
          },
          {
            name: 'wordBook',
            icon: 'book'
          },
          {
            name: 'addWord',
            icon: 'file-text-o'
          },
          {
            name: 'language',
            icon: 'adjust'
          }
        ]
      }
    },
    computed: {
      ...mapGetters({
        locales: 'getAvailableLocales',
        currentTheme: 'getCurrentTheme',
        currentComponent: 'getCurrentComponent'
      }),
      locale () {
        return this.$i18n.locale()
      }
    },
    methods: {
      changeComponent (component) {
        this.$store.dispatch('setCurrentComponent', component)
      },
      addNew () {
        this.$store.dispatch('setOldComponent', 'main')
        this.changeComponent('addWord')
      }
    },
    created: function () {
      // window.localStorage.clear()
      let lang = JSON.parse(window.localStorage.getItem('lang'))
      let $this = this
      clearTimeout(this._delayRunTimer)
      this._delayRunTimer = setTimeout(function () {
        if (lang) {
          $this.$i18n.set(lang)
        } else {
          $this.$i18n.set($this.locales[0])
        }
        $this.showGreet = false
      }, 1500)

      let wordsBook = JSON.parse(window.localStorage.getItem('wordBook'))
      if (wordsBook) this.$store.dispatch('setWordBook', wordsBook)

      let storageTheme = JSON.parse(window.localStorage.getItem('currentTheme'))
      if (storageTheme) {
        this.$store.dispatch('setCurrentTheme', storageTheme)
      }
    }
  }
</script>

<style lang="scss">
  @import './scss/main';
</style>

<style lang="scss" scoped>
  @import './scss/variables';
  .fade-leave-to{
    animation-duration: 0.5s;
  }
  .fade-enter-to{
    animation-duration: 0.5s;
  }
</style>



