<template>
  <div class="languages">
    <h3>{{ $t('Language of interface') }}</h3>
    <label v-for="(locale, index) in locales" :key="index">
      <input type="radio" name="lang" :value="locale" v-model="currentLocale">
      <span>{{ $t(locale) }}</span>
    </label>
    <div v-if="currentLocale !== storage" class="buttons">
      <a @click.prevent="changeLocale" class="button">{{ $t('keep') }}</a>
    </div>
    <h3>{{ $t('Themes') }}</h3>
    <label v-for="theme in themes" :key="theme.class">
      <input type="radio" name="theme" :value="theme.class" v-model="selectedTheme" @change="saveTheme()">
      <span>{{ $t(theme.name) }}</span>
    </label>
  </div>
</template>

<script>
  import { mapGetters } from 'vuex'
  export default {
    data () {
      return {
        storage: '',
        currentLocale: '',
        selectedTheme: ''
      }
    },
    computed: {
      ...mapGetters({
        locales: 'getAvailableLocales',
        currentTheme: 'getCurrentTheme',
        themes: 'getThemes'
      })
    },
    created () {
      this.currentLocale = this.$i18n.locale()
      this.storage = JSON.parse(window.localStorage.getItem('lang'))

      this.selectedTheme = JSON.parse(JSON.stringify(this.currentTheme))
    },
    methods: {
      changeLocale () {
        this.$i18n.set(this.currentLocale)
        this.storage = JSON.parse(JSON.stringify(this.currentLocale))
        window.localStorage.setItem('lang', JSON.stringify(this.currentLocale))
      },
      saveTheme () {
        this.$store.dispatch('setCurrentTheme', this.selectedTheme)
      }
    }
  }
</script>

<style lang="scss" scoped>
  @import './../scss/variables';
  label{
    display: block;
    position: relative;
    overflow: hidden;
    cursor: pointer;
    border-bottom: 1px solid $borderColor;
    input{
      position: absolute;
      opacity: 0;
      &:checked ~ span:before{
        content: '';
      }
    }
    span{
      display: block;
      position: relative;
      z-index: 1;
      padding: 15px 5px 6px 25px;
      &:before{
        content: '';
        display: inline-block;
        vertical-align: baseline;
        font-size: 1.2rem;
        width: 18px;
        color: $green;
        position: absolute;
        bottom: 4px;
        left: 7px;
      }
    }
  }
  .button{margin: 30px 0 0 0;}
</style>



