<template>
  <ul class="list">
    <li v-for="(folder , index) in wordBook.folders" :key="index">
      <a @click.prevent="openFolder(folder.id)">
        <div class="icon">
          <icon name="folder-o" scale="2"></icon>
          <span v-if="foldersLength[index] > 0">{{ foldersLength[index] }}</span>
        </div>
        <div class="name">{{ $t(folder.name) }}</div>
      </a>
    </li>
    <li>
      <a @click.prevent="openFolder(0)">
        <div class="icon">
          <icon name="folder-o" scale="2"></icon>
          <span>{{ wordBook.words.length }}</span>
        </div>
        <div class="name">{{ $t('All') }}</div>
      </a>
    </li>
  </ul>
</template>

<script>
  import { mapGetters } from 'vuex'
  import 'vue-awesome/icons/folder-o'
  export default {
    data () {
      return {}
    },
    computed: {
      ...mapGetters({
        wordBook: 'getWordBook'
      }),
      foldersLength () {
        let arr = []
        for (let i of this.wordBook.folders) {
          let thisId = i.id
          let wordArr = this.wordBook.words.filter(item => {
            if (item.folder === thisId) {
              return true
            }
          })
          arr.push(wordArr.length)
        }
        return arr
      }
    },
    methods: {
      openFolder (id) {
        this.$emit('setId', id)
      }
    }
  }
</script>

<style lang="scss" scoped>
  @import './../scss/variables';
  .list{
    list-style: none;
    padding: 0;
    li{
      display: block;
      & + li{
        border-top: 1px solid $borderColor;
      }
    }
    a{
      display: flex;
      align-items: center;
      padding: 5px 0 0 0;
      height: 45px;
      box-sizing: border-box;
    }
  }
  .icon{
    position: relative;
    span{
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      font-size: 11px;
      line-height: 9px;
      color: #717171;
    }
    .fa-icon {
      color: $borderColor;
      display: block;
    }
  }
  .name{
    line-height: 1;
    padding: 0 0 0 5px;
    font-size: 16px;
  }
</style>



